/* 
Styling of the Navbar itself (Desktop version)
*/
.navbar {
  display: flex;
  width: 100%;
  align-items: center;
  background-color: #282842;
  height: 5vh;
  /* position: fixed; */
}

.navbar > img {
  height: 80%;
  align-self: center;
  padding-right: 15px;
}

.navbar > a {
  padding: 0 1.5vw;
  font-size: 20px;
  text-decoration: none;
  color: white;
}

.navbar > a:hover {
  font-size: 22px;
  border-bottom: 2px solid white;
}

  /*
  Styling of the Icon to open the dropdown if Width < 700px
  */
  .navIcon {
    margin-left: auto;
    margin-right: 5px;
    display:none;
  }

  .navIcon:hover {
    cursor: pointer;
    border-bottom: 2px solid white;
  }

/*
Styling of the dropdown menu that appears on Icon Click (Mobile)
*/
  .dropdown {
    background-color: #282842;
    text-align: center;
  }

  .dropdown > a {
    text-decoration: none;
    color: white;
  }

  .dropdown > a:hover {
    font-size: 3vw;
    border-bottom: 2px solid white;
  }

  /*
  Media Breakpoints
  */
  @media screen and (max-width: 700px) {
    .Link {
      display: none;
    }
    .navIcon {
      display: block;
    }
  }